import axios from '@nextcloud/axios';
import NcCheckboxRadioSwitch from '@nextcloud/vue/dist/Components/NcCheckboxRadioSwitch.js';
import NcSettingsSection from '@nextcloud/vue/dist/Components/NcSettingsSection.js';
import NcSelect from '@nextcloud/vue/dist/Components/NcSelect.js';
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
import draggable from 'vuedraggable';
import DragVerticalIcon from 'vue-material-design-icons/DragVertical.vue';
import ArrowDownIcon from 'vue-material-design-icons/ArrowDown.vue';
import ArrowUpIcon from 'vue-material-design-icons/ArrowUp.vue';
import { loadState } from '@nextcloud/initial-state';
import { generateUrl } from '@nextcloud/router';
export default {
  name: 'AdminAI',
  components: {
    NcCheckboxRadioSwitch,
    NcSettingsSection,
    NcSelect,
    draggable,
    DragVerticalIcon,
    ArrowDownIcon,
    ArrowUpIcon,
    NcButton
  },
  data() {
    return {
      loading: false,
      dirty: false,
      groups: [],
      loadingGroups: false,
      sttProviders: loadState('settings', 'ai-stt-providers'),
      translationProviders: loadState('settings', 'ai-translation-providers'),
      textProcessingProviders: loadState('settings', 'ai-text-processing-providers'),
      textProcessingTaskTypes: loadState('settings', 'ai-text-processing-task-types'),
      settings: loadState('settings', 'ai-settings')
    };
  },
  computed: {
    hasStt() {
      return this.sttProviders.length > 0;
    },
    hasTextProcessing() {
      return Object.keys(this.settings['ai.textprocessing_provider_preferences']).length > 0 && Array.isArray(this.textProcessingTaskTypes);
    }
  },
  methods: {
    moveUp(i) {
      this.settings['ai.translation_provider_preferences'].splice(Math.min(i - 1, 0), 0, ...this.settings['ai.translation_provider_preferences'].splice(i, 1));
      this.saveChanges();
    },
    moveDown(i) {
      this.settings['ai.translation_provider_preferences'].splice(i + 1, 0, ...this.settings['ai.translation_provider_preferences'].splice(i, 1));
      this.saveChanges();
    },
    async saveChanges() {
      this.loading = true;
      const data = {
        settings: this.settings
      };
      try {
        await axios.put(generateUrl('/settings/api/admin/ai'), data);
      } catch (err) {
        console.error('could not save changes', err);
      }
      this.loading = false;
    },
    getTaskType(type) {
      if (!Array.isArray(this.textProcessingTaskTypes)) {
        return null;
      }
      return this.textProcessingTaskTypes.find(taskType => taskType.class === type);
    }
  }
};